.Label {
	height: 100%;
	text-align: center;
}

.MiddleOf {
	position: relative;
}

.Middle {
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
}

.Metrics {
	float: left;
	width: 16mm;
}

.Metric {
	box-sizing: border-box;
	color: white;
	height: 1.6em;
	padding: 0.2em;
	text-align: left;
	width: 1.6em;
}

.Metric .Value {
	font-size: 0.6em;
	line-height: 1.25em;
}

.Metric .Value.Big {
	line-height: 1.8em;
}

.Metric .Unit {
	font-size: 0.6em;
	margin-left: 0.25em;
}

.Metric .Name {
	font-size: 0.3em;
	line-height: 1.25em;
}

.SideNote {
	background-color: #333;
	box-sizing: content-box;
	color: #ccc;
	float: right;
	width: 6mm;
	font-size: 0.25em;
	padding: 0.5em;
	padding-left: 0;
	margin-right: -1mm;
	text-align: center;
	-webkit-transform: rotate(180deg);
	        transform: rotate(180deg);
	-webkit-writing-mode: vertical-rl;
	        writing-mode: vertical-rl;
}

.Details,.Recipe {
	float: right;
	font-size: 0.25em;
	margin: 0 2em;
	width: 20em;
	padding: 0 0.8em;
	text-align: justify;
}

.Details .NoDescription {
	-webkit-transform: translateY(-50%) rotate(90deg) translateY(-45%);
	        transform: translateY(-50%) rotate(90deg) translateY(-45%);
}

.Details .NoDescription .Ingredients,.Details .NoDescription .Logo {
	-webkit-transform: rotate(180deg);
	        transform: rotate(180deg);
}

.Details .Ingredients .Title {
	font-weight: bold;
}

.Details .Logo {
	font-size: 4em;
	text-align: center;
}

.Details .Logo .Image {
	display: inline-block;
	height: 1.5em;
	width: 5em;
}

.Details .Logo .Image img {
	max-height: 1.5em;
	max-width: 5em;
}

.Recipe {
	display: none;
}

.Recipe ul {
	padding: 0;
}

.Recipe li {
	list-style: none;
}

.Front {
	display: inline-block;
	height: 100%;
	overflow: auto;
	position: relative;
	text-align: left;
	width: 7em;
}

.Image.Placeholder {
	border: dashed 0.25em red;
	border-radius: 0.33em;
	box-sizing: border-box;
	text-align: center;
}

.Image.Placeholder::after {
	content: 'Drop image here';
	font-size: 0.5em;
	opacity: 0.5;
}

.Front .Image {
	height: 7em;
	width: 6em;
}

.Front .Image img {
	max-height: 7em;
	max-width: 6em;
}

.Image {
	overflow: hidden;
	position: relative;
}

.Image img {
	left: 50%;
	position: absolute;
	top: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}

.Front .Image p {
	font-size: 0.5em;
}

.Front .Title {
	bottom: 0;
	position: absolute;
	margin: 0.25em 0;
}

.Front .Name {
	font-size: 0.8em;
}

.Front .Style {
	font-size: 0.6em;
}

body {
	margin: 0;
}

:root {
	font-size: 1em;
}

.App {
	font-family: 'Quicksand', sans-serif;
	text-align: center;
}

.AppComponent {
	border-radius: 2.5mm;
	display: inline-block;
	/*
	box-shadow: 2.5mm 2.5mm rgba(24, 24, 24, 0.5)
	*/
}

.Main {
	background-color: white;
	font-size: 10mm;
	margin: 15vh auto 1em;
	overflow: hidden;
}

.Droparea {
	height: 100%;
	width: 100%;
}

.Controls {
	background: #444;
	box-sizing: border-box;
	color: #ccc;
	float: right;
	height: 100vh;
	margin-left: 1em;
	padding: 3em 2em;
	text-align: left;
}

.Controls .AppLogo {
	text-align: center;
}

.Controls .AppLogo img {
	max-height: 8em;
}

.Controls label {
	display: inline-block;
	min-width: 6em;
}

.Controls input, .Controls textarea {
	background-color: inherit;
	border: solid thin currentColor;
	color: inherit;
}

.Controls input[type=number], .Controls input[type=text] {
	-webkit-appearance: textfield;
	   -moz-appearance: textfield;
	        appearance: textfield;
	border: none;
	border-bottom: solid thin currentColor;
	font-size: inherit;
	margin: 0;
	margin-right: 0.33em;
	width: 4em;
}

.Controls textarea {
	border-radius: 0.5em;
	height: 15em;
	margin: 0.25em 0;
	padding: 0.125em;
	width: 100%;
}

.Controls input[type=button] {
	border-radius: 0.25em;
	font-size: 1.25em;
	padding: 0.25em 2em;
}

.Controls .Button {
	padding-top: 1.5em;
	text-align: center;
}

.Field,.Button {
	margin: 0.5em 0;
}

.Field input {
	margin: 0 0.5em;
}

.Background {
	background-color: #666;
	background-position: center;
	background-size: cover;
	height: 100%;
	left: 0;
	position: fixed;
	top: 0;
	width: 100%;
	z-index: -100;
}

.Label.Placeholder {
	font-size: 0.66em;
	opacity: 0.5;
	padding: 1.5em;
	text-align: center;
}

.Label.Placeholder::after {
	content: '';
}

